<template>
  <div>
   <div class="topTitle">
      <span class="topTitle-span1">数据提取</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">提取数据记录</span>
            <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">查看记录详情</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>申请信息
        <Button type="primary" style="float:right;margin-top:10px;margin-left:60px" @click="applyBtn">发起新申请</Button>
        <div class="stepBox">
          <div class="stepBoxCell" v-for="(item,index) in stepBox" :key="item.name">
            <div :class="item.flag==false?'stepBoxCell-1':'stepBoxCell-1-2'">{{index+1}}</div>
            <div class="stepBoxCell-2">{{item.name}}</div>
            <div class="stepBoxCell-3" v-if="stepBoxCell3(index)"></div>
          </div>
        </div>
        
      </div>
      <div class="contentCellBody">
        <Form
          ref="formValidate"
          :model="formValidate"
          :rules="ruleValidate"
          :label-width="120"
          inline
        >
          <Row>
            <FormItem label="提取数据期间：" prop="xxxx">
              <FormItem prop="extraTime">
                <DatePicker type="date" placeholder="请选择时间" :value="formValidate.extraTime" @on-change="formValidate.extraTime=$event"></DatePicker>
              </FormItem>
              <FormItem prop="extraTime2">
                <DatePicker type="date" placeholder="请选择时间" :value="formValidate.extraTime2" @on-change="formValidate.extraTime1=$event"></DatePicker>
              </FormItem>
            </FormItem>
            <FormItem label="提取机构：" prop="extraUnit">
              <Select v-model="formValidate.extraUnit" placeholder="请选择机构" style="width:200px">
                <Option v-for="item in extraUnits" :value="item.code" :key="item.code">{{item.name}}</Option>
              </Select>
            </FormItem>
            <FormItem label="业务范围" prop="busiScope">
              <Select v-model="formValidate.busiScope" placeholder="请选择业务" style="width:200px">
                <Option v-for="item in busiScopes" :value="item.code" :key="item.code">{{item.name}}</Option>
              </Select>
            </FormItem>
          </Row>
          <Row>
            <FormItem label="权限使用时间：" prop="xxxx">
              <FormItem prop="permissionTime">
                <DatePicker
                  type="date"
                  placeholder="请选择时间"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                ></DatePicker>
              </FormItem>
              <FormItem prop="permissionTime2">
                <DatePicker
                  type="date"
                  placeholder="请选择时间"
                  :value="formValidate.permissionTime2"
                  @on-change="formValidate.permissionTime1=$event"
                ></DatePicker>
              </FormItem>
            </FormItem>
            <FormItem label="紧急程度" prop="emergeLevel">
              <RadioGroup v-model="formValidate.emergeLevel">
                <Radio v-for="item in emergeLevels" :label="item.code" :key="item.code">{{item.name}}</Radio>
              </RadioGroup>
            </FormItem>
          </Row>
          <Row>
            <div  class="fujian">
              <div class="fujian-title">附件:</div>
              <p class="fujianself">央行63号文件</p>
            </div>
          </Row> 
        </Form>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>数据列表
      </div>
      <div class="contentCellBody">
        <Row style="margin-left:1px;margin-bottom:20px">
          <ButtonGroup>
            <Button type="primary">重新执行</Button>
          </ButtonGroup>
          <ButtonGroup >
            <Button type="primary">批量下载</Button>
          </ButtonGroup>
          <Dropdown style="margin-left:0.5px">
            <Button type="primary">
              <Icon type="ios-arrow-down"></Icon>
            </Button>
            <DropdownMenu slot="list">
              <DropdownItem>EXCEL</DropdownItem>
              <DropdownItem>MYSQL</DropdownItem>
              <DropdownItem>TXT</DropdownItem>
              <DropdownItem>CSV</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </Row>
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td>{{tableHeader.title1}}</td>
                <td class="textLeft">{{tableHeader.title2}}</td>
                <td>{{tableHeader.title3}}</td>
                <td>{{tableHeader.title4}}</td>
                <td>{{tableHeader.title5}}</td>
                <td>{{tableHeader.title6}}</td>
                <td>{{tableHeader.title7}}</td>
                <td class="textLeft">{{tableHeader.title8}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBody" :key="item.id">
                <td><Checkbox :checked.sync="item.flag"></Checkbox>{{index+1}}</td>
                <td class="textLeft">{{item.tableName}}</td>
                <td style="color:RGB(91,149,240); cursor: pointer;" @click="successfun(item)">{{item.succNum}}</td>
                <td style="color:RGB(91,149,240);cursor: pointer;" @click="errorfun(item)">{{item.errorNum}}</td>
                <td v-for="it in statusList"  v-if="it.code==item.execuStatus && it.name=='失败'" :key="it.code" style="color:RGB(250,174,98)">{{it.name}}</td>
                <td v-for="it in statusList" v-if="it.code==item.execuStatus && it.name=='成功'" :key="item.code" style="color:RGB(56,218,167)">{{it.name}}</td>
                <td v-for="it in statusList" v-if="it.code==item.execuStatus && it.name=='运行中'"  :key="item.code" style="color:RGB(230,230,230)">{{it.name}}</td>
                <td v-for="it in statusList" v-if="it.code==item.execuStatus && it.name=='等待'"  :key="item.code" style="color:RGB(230,230,230)">{{it.name}}</td>
                <td>{{item.execuStarttime}}</td>
                <td>{{item.execuEndtime}}</td>
                <td>{{item.execuApplicationnew}}</td>
                <td class="textLeft" v-for="it in statusList" v-if="it.code==item.execuStatus && it.name=='成功'" :key="it.code">
                  <span class="operationSpan">{{item.extend1}}</span>
                </td>
                <td class="textLeft" v-if="item.status=='失败'">
                  <span class="operationSpan" style="color:RGB(250,174,98)">重新执行</span>
                </td>
              </tr>
            </tbody>
          </table>
        </Row>
        
      </div>
    </div>
    <div style="width:100%;height:80px">
    </div>
  </div>
</template>
<script>
export default {
  created() {
      let that = this;
      let obj = this.$route.query;
      let data = JSON.stringify(obj);
      this.axios
        .post("/extract/opt/extractHisOpt", data, {
          headers: { "Content-Type": "application/json;charset=utf-8" }
        })
        .then(response => {
          if (response.code=="200"){
            let data0 = response.data.basicData;
            let data1 = response.data.recordDatas;
            that.formValidate = data0;
            that.tableBody = data1;
            for(let i=0; i<that.tableBody.length;i++){
               that.$set(that.tableBody[i], 'flag', false);
            }
          }
      });
       this.axios
        .get("/extract/getExtractDic/EXTRACT_UNIT,EMERCRGENCY_LEVEL,BUSI_SCOPE,EXECU_STATUS")
        .then(function(response) {
              if (response.code=="200"){
                  that.extraUnits = response.data.EXTRACT_UNIT;
                  that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
                  that.busiScopes = response.data.BUSI_SCOPE;
                  that.statusList = response.data.EXECU_STATUS 
              }
        })
        .catch(function(error) {});
  },
  data() {
    return {
      stepBox: [
        { name: "申请", flag: false },
        { name: "复核", flag: false },
        { name: "分公司审批", flag: false },
        { name: "部门审批", flag: false },
        { name: "合规审批", flag: false }
      ],
      tableHeader: {
        title1: "序",
        title2: "检查表名称",
        title3: "成功数据量",
        title4: "错误数据量",
        title5: "执行状态",
        title6: "执行开始时间",
        title7: "执行结束时间",
        title8: "操作"
      },
      tableBody: [
      ],
      formValidate: {
        xxxx: "占位",
        workflowId: "12232",
        batchId: "23223",
        extraTime: "", //提取数据时间
        extraTime2: "", //提取数据时间
        extraUnit: "", //机构
        busiScope: "", //业务
        permissionTime: "", //期限时间
        permissionTime2: "", //期限时间
        emergeLevel: "" //紧急
        // -----
      },
      extraUnits: [
      ],
      busiScopes: [
      ],
      emergeLevels: [
      ],
      statusList: [

      ],
      ruleValidate: {
        xxxx: [
            {
              required: true,
              message: '此项不能为空',
              trigger: 'blur'
            },
        ],
        extraUnit: [
          {
            required: true,
            message: "请选择机构",
            trigger: "change"
          }
        ],
        busiScope: [
          {
            required: true,
            message: "请选择业务",
            trigger: "change"
          }
        ],
        emergeLevel: [
          { required: true, message: "请选择紧急程度", trigger: "change" }
        ],
        extraTime: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        extraTime1: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime1: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    stepBoxCell3(index) {
      let index2 = index + 1;
      if (index2 == this.stepBox.length) {
        return false;
      } else {
        return true;
      }
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
            let data = JSON.stringify(this.formValidate);
            this.axios
              .post("/extract/extractApplication", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(res => {
              })
              .catch(error => {
                alert(2)
              });
        } else {
        }
      });
    },
    ceshi(){
      
    },
    applyBtn(){
        this.$router.push({ name: "dataExtractionApply" });
    },
    successfun(item){
       this.$router.push(
         {
          name: "dataExtractionPreview",
          query: {
            type:'success',
            batchId:item.batchId,
            isSuc:'0',
            reDoNum:item.execuApplicationNew,
            tableName:item.tableField
          }
         }
        );
    },
    errorfun(item){
        this.$router.push(
         {
          name: "dataExtractionPreview",
          query: {
            type:'error',
            batchId:item.batchId,
            isSuc:'1',
            reDoNum:item.execuApplicationNew,
            tableName:item.tableField
          }
         }
        );
    }
  }
};
</script>
<style scoped>
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.fujian{
  padding-left: 122px;
  position: relative;
}
.fujian-title{
  position: absolute;
  left:73px;
}
.fujianself{
  width:250px;
  height: 20px;
  border-radius: 4px;
  padding:2px 20px;
  background-color:RGB(238,243,248);
  cursor: pointer;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
</style>


